<template>
  <div>
    <headerBar :header-data="headerData" />
    <div v-if="pageStyles===0" class="container">
      <div class="content">

        <div v-for="(item,index) of dsjjjgXq" :key="index" class="common-main box overflow-hidden relative">
          <div v-if="item.sfzb=='1'" class="cornermark">中标</div>
          <div class="page-list">
            <div class="pagelist-left">供应商名称</div>
            <div class="pagelist-right">{{ item.gysmc }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">联系人</div>
            <div class="pagelist-right">{{ item.lxr }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">联系电话</div>
            <div class="pagelist-right">{{ item.lxfs }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">中标金额(元)</div>
            <div class="pagelist-right">{{ item.zbje }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">状态</div>
            <div class="pagelist-right">{{ item.sfzb===1?'中标':'未中标' }}</div>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
  </div>
</template>

<script land="script" scoped>
import { dsjjjgXq } from '@/store/module.js'
import headerBar from '@/components/header/headerBar'
import errorPage from '@/components/result-page/index'
export default {
  name: 'XjMemberList',
  components: {
    errorPage,
    headerBar
  },
  data() {
    return {
      headerData: {
        title: '竞价结果详情',
        leftUrl: 'back',
        leftText: ' ',
        rightText: '报价历史',
        rightUrl: 'BidMemberHistory'
      },
      pageStyles: 0,
      dsjjjgXq: [],
      xmxh: ''
    }
  },
  computed: {

  },
  created() {
    this.xmxh = this.$store.state.bid.xmxh
    this.getJjjgXq()
  },

  methods: {
    parentReceive() {
      this.getJjjgXq()
    },
    getJjjgXq() {
      dsjjjgXq.data = { xmxh: this.xmxh }
      this.asyncApiModule(dsjjjgXq).then(res => {
        this.dsjjjgXq = res
        if (this.dsjjjgXq.length > 0) {
          this.pageStyles = 0
        } else {
          this.pageStyles = 1
        }
      }).catch(error => {
        this.pageStyles = 3
        console.log(error)
      })
    }
  }

}
</script>

<style lang="less" scoped>

</style>
